e-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <table class=\"table\">\n        <thead>\n          <tr>\n            <th>订单号</th>\n            <th>用户姓名</th>\n            <th>用户电话</th>\n            <th>提现金额</th>\n            <th>申请时间</th>\n            <th>操作</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr v-for=\"(item,index) in list\"\n              :key=\"index\">\n            <td>{{item.odd_number}}</td>\n            <td class=\"t-l\">{{item.name}}</td>\n            <td>{{item.phone}}</td>\n\n            <td>{{item.money}}</td>\n            <td>{{item.create_time}}</td>\n            <td>\n              <a href=\"javascript:;\"\n                 @click=\"income(item.unionId)\">收入明细</a>\n              <a href=\"javascript:;\"\n                 @click=\"withdraw(item.unionId)\">提现明细</a>\n              <a href=\"javascript:;\"\n                 @click=\"check(currentIndex=index,item.unionId,item.money)\">审核</a>\n            </td>\n          </tr>\n        </tbody>\n      </table>\n      <!-- 分页 -->\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n    <el-dialog title=\"收入明细\"\n               :visible.sync=\"centerDialogVisible\"\n               center>\n      <table class=\"table\">\n        <thead>\n          <tr>\n            <th>收入类型</th>\n            <th>收入金额</th>\n            <th>收入时间</th>\n            <th>提现状态</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr v-for=\"item in incomelist\">\n            <td>{{item.type==1 ? '直推奖励' : item.type==2 ? '间退奖励' : '中心奖励'}}</td>\n            <td>{{item.money}}</td>\n            <td>{{item.create_time}}</td>\n            <td>{{item.cash_status==0 ? '未提现' : item.cash_status==1 ? '已提现' : item.cash_status==2 ? '提现审核中' : '提现驳回'}}</td>\n          </tr>\n        </tbody>\n      </table>\n      <span slot=\"footer\"\n            class=\"dialog-footer\">\n        <paging :page-count=\"pagearr\"\n                :page=\"index1\"\n                @index=\"paging1\"></paging>\n      </span>\n    </el-dialog>\n    <el-dialog title=\"提现明细\"\n               :visible.sync=\"centerDialogVisible1\"\n               center>\n      <table class=\"table\">\n        <thead>\n          <tr>\n            <th>提现订单号</th>\n            <th>提现金额</th>\n            <th>提现时间</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr v-for=\"item in withlist\">\n            <td>{{item.odd_number}}</td>\n            <td>{{item.money}}</td>\n            <td v-if=\"item.audit_time!=''\">{{item.audit_time}}</td>\n            <td v-else>--</td>\n          </tr>\n        </tbody>\n      </table>\n      <span slot=\"footer\"\n            class=\"dialog-footer\">\n        <paging :page-count=\"pagearr1\"\n                :page=\"index2\"\n                @index=\"paging2\"></paging>\n      </span>\n    </el-dialog>\n\n    <div class=\"laybox2\"\n         style=\"display:none\">\n      <ul>\n        <li>上次提现时间：{{obj.audit_time}}</li>\n        <li>上次提现金额：{{obj.last_money}}</li>\n        <li>本次提现金额：{{obj.money}}</li>\n      </ul>\n      <div class='bot t-c'>\n        驳回理由:<input type=\"text\"\n               placeholder=\"请输入驳回理由\"\n               name=\"reason\"\n               v-model=\"reason\">\n        <button class=\"reject\"\n                @click=\"reject\">驳回</button>\n        <button class='pass'\n                @click=\"pass\">通过</button>\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport page from '@/components/common.js';\nexport default {\n  data () {\n    return {\n      //分页数据\n      page: 1, //显示的页码数\n      totalpage: 1, //总页数\n      pageCount: 1,\n      index: 1, //点击的当前页，\n      endpage: 1, //当前一组页码的最大值\n      arr: [], //默认显示 1 2 3 4 5页\n      n: 1, //当前点击的页码的位置\n      list: [],\n      pagearr: 1,\n      pagearr1: 1,\n      index1: 1,\n      index2: 1,\n      incomelist: [],\n      withlist: [],\n      obj: {},\n      reason: '',\n      id: '',\n      aid: '',\n      token: window.sessionStorage.getItem('bbytoken'),\n      authList: [],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      currentIndex: '',\n      Trial_open: '',\n      unionId: '',\n      sunionId: '',\n      centerDialogVisible: false,\n      centerDialogVisible1: false\n    }\n  },\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    paging1 (e) {\n      this.index1 = e;\n\n      this.income(this.sunionId)\n    },\n    paging2 (e) {\n      this.index2 = e;\n      this.withdraw(this.unionId)\n    },\n    init: function () {\n      this.$axios.post('admin/UbiForward/apply', {\n        token: this.token,\n        page: this.page\n      })\n        .then(res => {\n\n          if (res.data.code == 1) {\n            this.list = res.data.data.list;\n\n            this.pageCount = res.data.data.rows;\n          } else {\n            layer.msg(res.data.msg)\n            this.list = []\n          }\n        })\n        .catch(err => { })\n    },\n    income: function (unionId) {\n      this.sunionId = unionId\n\n      this.$axios.post('admin/UbiForward/incomeDetails', {\n        token: this.token,\n        unionId: unionId,\n        page: this.index1\n      })\n        .then(res => {\n\n          if (res.data.code == 1) {\n            this.centerDialogVisible = true\n            this.incomelist = res.data.data.list;\n            this.pagearr = res.data.data.rows;\n          } else {\n            layer.msg(res.data.msg)\n          }\n\n        }).catch(err => { })\n    },\n    withdraw: function (unionId) {\n      this.unionId = unionId\n\n      this.$axios.post('admin/UbiForward/presentationDetails', {\n        token: this.token,\n        unionId: unionId,\n        page: this.index2\n      })\n        .then(res => {\n          if (res.data.code == 1) {\n            this.centerDialogVisible1 = true\n            this.withlist = res.data.data.list;\n            this.pagearr1 = res.data.data.rows;\n          } else {\n            layer.msg(res.data.msg)\n          }\n\n        }).catch(err => { })\n    },\n    check: function (index, unionId, money) {\n     \n      this.$axios.post('admin/UbiForward/adoptDetail', {\n        token: this.token,\n        unionId: unionId,\n        money: money\n      })\n        .then(res => {\n\n          this.obj = res.data.data;\n          this.Trial_open = layer.open({\n            type: 1,\n            area: ['50%', '60%'],\n            title: ['审核', 'height:60px;background-color:#98D1F9;color:white;text-align:center;font-size:20px;line-height:60px;'],\n            shadeClose: true,\n            shade: 0.8,\n            closeBtn: 1, //怎么显示 ×\n            content: $('.laybox2')\n          })\n        })\n    },\n    reject: function () {\n      let { unionId, id, phone, name: user, create_time, money } = { ...this.list[this.currentIndex] }\n      let data = Object.assign({ token: this.token, reason: this.reason }, { unionId, id, phone, user, create_time, money })\n      if (!this.reason) {\n        layer.msg('驳回理由不能为空')\n        return;\n      }\n      var index = parent.layer.getFrameIndex(window.name); //获取窗口索引 \n\n      this.$axios.post('admin/UbiForward/dismissalOperation', data)\n        .then(res => {\n          if (res.data.code == 1) {\n\n            layer.msg(res.data.msg, { time: 1500 }, (() => {\n              layer.close(this.Trial_open);\n            }));\n            this.init()\n          } else {\n            layer.msg(res.data.msg)\n          }\n\n        })\n        .catch(err => console.log(err))\n    },\n    pass: function () {\n      let { id, phone, unionId, name } = { ...this.list[this.currentIndex] }\n      let data = Object.assign({ token: this.token }, { id, phone, unionId, name })\n      this.$axios.post('admin/UbiForward/adoptPost', data)\n        .then(res => {\n\n          if (res.data.code == 1) {\n            layer.closeAll();\n            layer.msg(res.data.msg);\n            this.init()\n          } else {\n            layer.msg(res.data.msg)\n          }\n        })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == 'UBI提现') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '申请列表' && arr[i].name == 'UBI提现') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n  created () {\n    this.init();\n  },\n\n}\n</script>\n<style scoped>\n.laybox2 ul {\n  margin: 33px auto 33px auto;\n  border-bottom: 1px dashed #04bbfc;\n}\n\n.laybox2 {\n  padding: 0 35px;\n  font-size: 16px;\n}\n\n.laybox2 li {\n  width: 300px;\n  text-align: left;\n  margin: 33px auto;\n}\n\ndiv.bot {\n  margin: 45px auto 35px auto;\n}\n\ndiv.bot input {\n  display: inline-block;\n  width: 85%;\n  outline: none;\n  border: none;\n  border-bottom: 1px solid #aaa;\n  margin-left: 11px;\n}\n\ndiv.bot button {\n  margin-top: 38px;\n  width: 110px;\n  height: 36px;\n  border-radius: 18px;\n  box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n  border: none;\n  color: white;\n}\n\nbutton.reject {\n  background: rgba(237, 81, 55, 1);\n  margin-right: 30%;\n}\n\nbutton.pass {\n  background-color: #20a0ff;\n}\n</style>"],"sourceRoot":""}]);

